<template>
  <div class="header-panel">
        <mt-header fixed title="">
            <mt-button icon="back" slot="left" @click="$router.go(-1)"></mt-button>
        </mt-header>
        <div class="mt-content">
          <form class="" :action="action" method="get">
            <input type="hidden" name="uid" :value="uid"/>
            <input type="hidden" name="returnUrl" :value="returnUrl"/>
            <input type="hidden" name="bankCode" :value="bankCode">
            <input type="hidden" name="bankName" :value="bankName"/>
            <input type="hidden" name="cardNo" :value="cardNo"/>
              <div class="mt-input-row" @click="showModal">
                <label style="font-size:16px;"></label>
                <span style="display:inline-block;width:80px;">{{bankName}}</span>
                <img src="../../assets/images/xiala.png" style="height:15px;float:right;padding-top:15px;">
              </div>
              <mt-field label="" placeholder="" type="text" v-model="cardNo"></mt-field>
              <div @click="hideModal" id="hideAnniu" style="display:none;position:fixed;z-index:1000;background:#eee;bottom:150px;right:20px;"></div>
              <mt-picker id="modal" style="display:none;position:fixed;width:100%;bottom:0;z-index:999;background:#eee;" :slots="slots" @change="onValuesChange"></mt-picker>
              <button style="background:#FE5A59;color:#fff;border:0;border-radius:25px;height:41px;width:90%;margin:100px 5% 0 5%;" type="submit"> </button>
          </form>
        </div>
  </div>
</template>
<style>
.picker.active {
	-webkit-transform: translateY(0px);
}
.mint-cell{
    min-height:48px;
}
</style>
<script>
import { Toast } from 'mint-ui';
export default {
   data() {
    return {
        action:'',
        uid:'',
        returnUrl:'',
        bankCode:'',
        bankName:'',
        cardNo:'',
        slots: [
        {
          flex: 1,
          values: [

          ],
          className: 'slots',
        }
        ],
    }
  },
  created:function(){
    this.getBankDetail();
    this.bindBank();
  },
  methods: {
    onValuesChange(picker,values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1,values[0]);
      }
      var bankName=picker.getValues()[0];
      this.bankName=bankName;
      switch(this.bankName){
        case '':
          this.bankCode="ABC";
          break;
        case '':
          this.bankCode="BOC";
          break;
        case '':
          this.bankCode="BOS";
          break;
        case '':
          this.bankCode="CBHB";
          break;
        case '':
          this.bankCode="CCB";
          break;
        case '':
          this.bankCode="CEB";
          break;
        case '':
          this.bankCode="CIB";
          break;
        case '':
          this.bankCode="CITIC";
          break;
        case '':
          this.bankCode="CMB";
          break;
        case '':
          this.bankCode="CMBC";
          break;
        case '':
          this.bankCode="COMM";
          break;
        case '':
          this.bankCode="CSCB";
          break;
        case '':
          this.bankCode="CZB";
          break;
        case '':
          this.bankCode="CZCB";
          break;
        case '':
          this.bankCode="EGBANK";
          break;
        case '':
          this.bankCode="GDB";
          break;
        case '':
          this.bankCode="GNXS";
          break;
        case '':
          this.bankCode="GZCB";
          break;
        case '':
          this.bankCode="HCCB";
          break;
        case '':
          this.bankCode="HKBCHINA";
          break;
        case '':
          this.bankCode="HKBEA";
          break;
        case '':
          this.bankCode="HSBANK";
          break;
        case '':
          this.bankCode="HXB";
          break;
      }
    },
    showModal(){
      document.getElementById('modal').style.display="block";
      document.getElementById('hideAnniu').style.display="block";
    },
    hideModal(){
      document.getElementById('modal').style.display="none";
      document.getElementById('hideAnniu').style.display="none";
    },
    getBankDetail(){
      this.$http.post(this.$store.state.host+'/vendor/payment/bank',{}).then(response => {
          var resp=response.body;
          if(resp.code==0){
            for(var i=0;i<resp.data.length;i++){
              var arr={};
              arr=resp.data[i].bankName;
              this.slots[0].values.push(arr);
              
            }
          }
      })
    },
    bindBank(){
      this.action=this.$store.state.host+'/vendor/payment/auth';
      this.uid=JSON.parse(localStorage.getItem("MEMBER")).id;
      
      this.bankCode=this.bankCode;
      this.returnUrl='http://weixin.tjsudai.com/#/attention';
    }

  }
 
}
</script>


