<template>
  <div class="header-panel">
        <mt-header fixed title="">
            <mt-button icon="back" slot="left" @click="$router.go(-1)"></mt-button>
            <router-link to="tixianRecord" slot="right">
                <mt-button style="background: none;width:100%;"></mt-button>
            </router-link>
        </mt-header>
        <div class="mt-content" style="margin-top:30px;">
            <div class="mt-input-row" style="color:#000;">
				<label style="font-size:16px;"></label>
				<span style="display:inline-block;padding-right: 20px;">{{yue}}</span>
			</div>
            <mt-field label="" placeholder="" type="text" v-model="jine"></mt-field>
            <mt-button class="small" style="border-radius:0;margin-top:50px;" @click="withdraw"></mt-button>
        </div>
  </div>
</template>
<style>
    #yue .mint-field-other{
        width:70%;
        color:#000;
    }
    .mt-input-row label{
        text-align:left;
    }
</style>
<script>
import { Toast } from 'mint-ui';
    export default {
        data(){
            return{
                yue:'',
                jine:''
            }
        },
        created:function(){
            this.getTixian();
        },
        methods:{
            getTixian(){
                this.$http.post(this.$store.state.host+'/app/uc/member/detail.do',{}).then(response => {
                    var resp=response.body;
                    if(resp.code==0){
                        this.yue=resp.data.commission;
                    }else{
                        Toast(resp.message);
                    }
                })
            },
            withdraw(){
                if(this.jine==''){
                    Toast('');
                    return false;
                }
                this.$http.post(this.$store.state.host+'/app/uc/commission/withdraw',{amount:this.jine}).then(response => {
                    var resp=response.body;
                    Toast(resp.message);
                    if(resp.code==0){
                        this.$router.push('/home');
                    }
                })
            }
        }
    }
</script>




